<template>
  <div v-for="shop in shopInfo" :key="shop.bnum">
    <router-link :to="{ name: 'C_ShopInf', params: { id: shop.bnum } }">
      <div class="card h-100">
        <div class="col">
          <div class="card h-100">
            <div class="card-body d-flex align-items-center">
              <div class="StoreImg">
                <!-- <img
                  :src="menu.fimg"
                  class="card-img-top"
                  alt="Restaurant Image"
                /> -->
              </div>
              <div class="ml-3" style="margin-left: 20px">
                <h5 class="card-title mb-0">{{ shop.sname }}</h5>
                <p class="card-text">
                  운영시간 : {{ shop.start_time }} ~ {{ shop.end_time }}
                </p>
                <p class="card-text">최소주문 100원 부터</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </router-link>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "C_ShopCard",
  data() {
    return {
      // snames: [], // 상점 목록을 저장할 배열
      shopInfo: [], // 상점 정보를 저장할 객체
    };
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      await axios
        .get("http://localhost:8888/C_ShopCard") // 백엔드에서 메뉴 데이터를 제공하는 엔드포인트로 수정
        .then((response) => {
          this.shopInfo = response.data; // 응답 데이터를 'menus' 배열에 저장
        })
        .catch((error) => {
          console.error("Error fetching menu data:", error);
        });
    },
  },
};
</script>


<style scoped>
.card {
  border: 1px solid #dee2e6;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
}

.StoreImg {
  /* max-width: 100%;
  width: 100px; */
  height: auto;
}

.card-body {
  padding: 20px;
}

.card-title {
  font-size: 1.2rem;
  font-weight: bold;
}

.card-text {
  color: #6c757d;
  font-size: 0.9rem;
}
</style>
